<template>
  <div class="box">
    <div class="nav">
      <div class="go-back">
        <img src="../../public/img/h_btn_back.png" @click="goHome">
      </div>
      <div class="text">
        <span>选择城市</span>
      </div>
    </div>
    <div class="seek">
      <div>
        <img src="../../public/img/search_ico_01.png">
        <input type="text" placeholder="请输入搜索关键字" ref="input" @change="MatchCity('搜索')" @input="inputValue">
      </div>
    </div>
    <div class="city-two" v-show="cityShow">
      <div class="at-present-city">
        <div class="name">
          <span>当前城市:</span>
        </div>
        <div class="item">
          <p>定位失败...</p>
        </div>
      </div>
      <div class="hot-city">
        <div class="name">
          <span>热门城市:</span>
        </div>
        <div class="item hot">
          <span v-for="(item, index) in hotCity" :key="index" @click="MatchCity(item)">{{item}}</span>
        </div>
      </div>
    </div>

    <div class="citylist" v-for="(item, index) in addressEnglish" :key="index">
      <div class="name">
        <span>{{item.toUpperCase()}}</span>
      </div>
      <div class="item list" v-if="cityData">
        <span v-for="(text, ind) in cityData[index].data" :key="ind" @click="SelectCity(text.n, text.id)">{{text.n}}</span>
        <!-- v-for="(text, ind) in cityData.index" :key="ind" -->
      </div>
    </div>

    <div class="hint" v-show="!cityShow">
      <div v-show="inputDataShow">当前无此城市</div>
      <div v-for="(item, index) in inputData" :key="index">{{item.n}}</div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Site",
  data() {
    return {
      hotCity: [
        "上海",
        "深圳",
        "重庆",
        "北京",
        "成都",
        "广州",
        "杭州",
        "东莞",
        "武汉"
      ], 
      //地址数据
      addressData: "",
      //首字母
      ABC: "",
      //按首字母分类后的城市数据
      cityData: "",
      //定位与热门城市是否显示
      cityShow: true,
      //搜索框提示的数据
      inputData: "",
      //没有指定城市提示
      inputDataShow: false
    };
  },
  computed: {
    //取地址英文名的首字母
    addressEnglish() {
      let b = [];
      for (var i = 0; i < this.addressData.length; i++) {
        if (b.indexOf(this.addressData[i].pinyinShort.substring(0, 1)) == -1) {
          b.push(this.addressData[i].pinyinShort.substring(0, 1));
        }
      }
      if (b !== false) {
        this.ABC = b.sort()
      }
      return b.sort()
    }
  },
  watch: {
    //地址列表
    ABC() {
      var a = this.ABC;
      var b = []
      if (this.addressData) {
        for (var i = 0; i < a.length; i++) {
          let num = this.addressData.filter(function(element) {
            return element.pinyinShort.substring(0, 1) == a[i];
          }); 
           b.push({data : num})
        }
        this.cityData = b
      }
    },
  },
  methods: {
    //返回主页
    goHome() {
      this.$router.push({path: '/'})
    },
    //选择城市
    SelectCity(cityName, cityId) {
      this.$store.dispatch('SelectCity', {cityName: cityName, cityId: cityId})
      this.$router.push({path: "/"})
    },
    //搜索匹配城市
    MatchCity(cityName) {
      //搜索框搜索情况
      if(cityName == '搜索') {
        let b = this.$refs.input.value
        let a = this.addressData.filter(function(element) {
         return  element.n == b
        })
        if(a[0]) {
          this.$store.dispatch('SelectCity', {cityName: b, cityId: a[0].id})
          this.$router.push({path: "/"})
        }
        
      } else {
        //直接点击热门城市时
        let a = this.addressData.filter(function(element) {
         return  element.n == cityName
        })
        this.$store.dispatch('SelectCity', {cityName: cityName, cityId: a[0].id})
        this.$router.push({path: "/"})
      }
      
    },
    //判断搜索框中是否有内容
    inputValue() {
      let a = this.$refs.input.value
      if(this.$refs.input.value) {
        this.cityShow = false
      } else {
        this.cityShow = true
      }
      this.inputData = this.addressData.filter(function(element) {
         return element.pinyinFull.toLowerCase().indexOf(a) != -1
      })
      if(this.inputData.length == 0) {
        this.inputDataShow = true
      } else {
        this.inputDataShow = false
      }
    },
  },
  created() {
    //请求地址数据
    this.axios.get("/api/Showtime/HotCitiesByCinema.api").then(response => {
      this.addressData = response.data.p;
    });
  }
};
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
@import '../../public/sass/mixin';
.box {
  position: relative;
  margin-top: -54px;
  width: 100%;
}

.nav {
  display: flex;
  width: 100%;
  height: 54px;
  background: #1c2635;

  .go-back {
    position: relative;
    flex: 0 0 54px;
    height: 54px;

    img {
      display: inline-block;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 14px;
    }
  }

  .text {
    flex: 1;
    height: 100%;
    line-height: 54px;
    text-align: center;

    span {
      margin-left: -54px;
      font-size: 18px;
      color: #fff;
    }
  }
}

.seek {
  padding: 10px 18px;
  width: calc(100% - 36px);
  height: 40px;
  @include border-bottom(rgb(216, 216, 216));
  background: rgb(246, 246, 246);

  div {
    position: relative;
    display: flex;
    padding-left: 34px;
    height: 100%;
    border: 1px solid #bbb;
    border-radius: 10px;
    background: #fff;

    img {
      display: inline-block;
      flex: 0 0 22px;
      position: absolute;
      left: 10px;
      top: 50%;
      transform: translateY(-50%);
      height: 22px;
    }

    input {
      margin-left: 5px;
      outline: none;
      border: none;
      font-size: 16px;
    }
  }
}

.city-two {
  width: 100%;
}

.city-two .at-present-city,
.city-two .hot-city,
.citylist {
  width: 100%;

  .name {
    padding: 0 17px;
    width: calc(100% - 34px);
    height: 35px;
    background: #ebebeb;
    color: #777;

    span {
      display: inline-block;
      height: 100%;
      line-height: 35px;
      font-size: 18px;
    }
  }

  .item {
    padding: 0 17px;
    width: calc(100% - 34px);
    height: 54px;

    p {
      height: 100%;
      line-height: 54px;
      font-size: 18px;
      color: #333;
    }
  }

  .item.hot,
  .item.list {
    padding-bottom: 17px;
    display: flex;
    flex-wrap: wrap;
    height: auto;

    span {
      flex: 0 0 25%;
      padding-top: 18px;
      font-size: 18px;
      color: #333;
      text-align: center;
    }
  }
}

.hint {
  position: absolute;
  top: 114px;
  left: 0;
  width: 100%;
  height: calc(100% - 114px);
  z-index: 10;
  background: #fff;

  div {
    margin-left: 18px;
    height: 40px;
    line-height: 40px;
    @include border-bottom(rgb(216, 216, 216));
    box-sizing: border-box;
    font-size: 18px;
    color: #333;
  }
}
</style>
